
import Tab from 'vux/src/components/tab/tab.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default{
components:{
Tab,
Swiper,
TabItem,
SwiperItem,
Sticky,
XImg
},
data(){
return{
titles:["正在热映","即将上映"],
index:0,
lists:{},
}
},
methods:{
getData:function(){
this.$http.get("/wemovie/movie.php?act=movies").then((res)=>{
if(res.data.code==200){
this.lists=res.data.data;
this.$nextTick(function(){
var mtswiperbox=$(".mt-hot-swiper-box0").height()+10;
this.$refs.swiper.xheight=mtswiperbox+'px'
})
}
console.log(res)
})
},
setHeight:function(currentIndex){
if(currentIndex==0){
var mtswiperbox=$(".mt-hot-swiper-box0").height()+10;
}else{
var mtswiperbox=$(".mt-hot-swiper-box1").height()+10;
}
this.$refs.swiper.xheight=mtswiperbox+'px';
},
toMtDetail:function(movie_id,index){
if(index==0){
this.$router.push({
name:"mtDetail",
params:{
movie_id:movie_id
}
})
}
}
},
created:function(){
this.getData();
},
}



