
import Icon from 'vux/src/components/icon/index.vue'

export default{
components:{
Icon
},
data(){
return{
cinemaid:this.$route.query.cinemaid,
movieid:this.$route.query.movieid,
params:this.$route.query,
cinemaDetail:{},
movies:[],
movieBg:"",//切换电影改变背景
time:{},//日期
plan:[],//选择影厅
slideIndex:0,//滑动切换
}
},
methods:{
getData:function(){
var self=this;
this.$http.get("/wemovie/movie.php?act=allCinemaDetail",{params:this.params}).then((res)=>{
if(res.data.code==200){
var data=res.data.data;
this.cinemaDetail=data.cinemaDetail;
this.movies=data.movies;
this.time=data.time;
this.plan=data.plan;
var movieBgIndex=0;
var movieArr=this.movies;
for(var j = 0,len=movieArr.length; j < len; j++) {
if(movieArr[j].active==1){
movieBgIndex=j;
break;
}
}
this.$nextTick(function(){
var mySwiper = new Swiper('.swiper-container',{
slideToClickedSlide:true,
slidesPerView: 4,
centeredSlides: true,
paginationClickable: true,
spaceBetween: 15,
onSlideChangeEnd: function(swiper){
var slideIndex=swiper.activeIndex;
var movie_id=swiper.slides[slideIndex].getAttribute("data-id");
self.params.movieid=movie_id;
self.changeData();
}
});
mySwiper.slideTo(movieBgIndex, 500, false);
})
}else{
alert(res.data.message)
}
console.log(res)
})
},
selectkMovie:function(movie_id){
datas.movieid=movie_id;
this.changeData();
},
changeData:function(){
this.$http.get("/wemovie/movie.php?act=allCinemaDetail",{params:this.params}).then((res)=>{
if(res.data.code==200){
console.log(res)
var data=res.data.data;
this.cinemaDetail=data.cinemaDetail;
this.movies=data.movies;
this.time=data.time;
this.plan=data.plan;
}else{
alert(res.data.message)
}
})
},
selectDate:function(date){
this.params.date=date;
this.changeData();
},
toSeat:function(id){
this.$router.push({
name:"mtSeat",
params:{
id:id
}
})
}
},
created:function(){
this.params.date=this.params.date;
this.params.cinemaid=this.params.cinemaid;
this.params.movieid=this.params.movie_id;
this.getData();
},
}




