export default{
	created:function(){
		this.$http.get("/wemovie/order.php").then((res)=>{
			if(res.data.code==200){
				console.log(res)
				this.list=res.data.data;
			}else{
				console.log(res)
				this.orderNone=true
			}
		})
	},
	data(){
		return {
			list:[],
			orderNone:false,//是否有订单 默认为false
		}
	},
	methods:{
		toOrderDetail:function(order_id){
			this.$router.push({
				name:"orderDetail",
				params:{
					order_id:order_id
				}
			})
		}
	}
}



