
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

var timer;
export default {
data() {
return {
appId: "",
timestamp: "",
nonceStr: "",
signature: '',
order: {},
cards: [],
countDown_minute: "",
countDown_second: "",
selectAdd: false,
valArr: [], //选择的id数组
valObj: {}, //选择的id以及数量对象
order_id: this.$route.params.order_id,
}
},
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton,
XNumber,
XDialog,
Group,
Cell
},
created: function() {
this.getCardList();
},
methods: {
getwxQRCode: function() {
var self = this;
self.$wechat.scanQRCode({
needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
success: function(res) {
var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
self.$http.get("/wemovie/wxscancode.php?act=getCodeParams", {
params: {
codeUrl: result
}
}).then((res) => {
if(res.data.code == 200) {
self.$router.push({
name: "addCard",
params: {
card_no: res.data.data.card_no,
card_number: res.data.data.card_number
}
})
} else {
alert(res.data.message)
}
})
}
});
},

getCardList: function() {
var _self = this;
this.$http.post("/wemovie/card_order.php?act=index", {
order_id: this.order_id
}).then((res) => {
console.log(res)
if(res.data.code == 200) {
this.order = res.data.data.order;
this.cards = res.data.data.cards;
for(var i=0; i<this.cards.length;i++){
this.cards[i].checked=false;
}
var time = res.data.data.order.end_paytime;
timer = setInterval(function() {
if(time >= 0) {
var minute = Math.floor((time / 60) % 60);
var second = Math.floor(time % 60);
minute = minute < 10 ? "0" + minute : minute;
second = second < 10 ? "0" + second : second;
_self.countDown_minute = minute;
_self.countDown_second = second;
time--;
console.log('定时器',1)
} else {
clearInterval(timer);
alert("订单已超时！")
};
}, 1000);
} else {
alert(res.data.message)
}
})
},
sub:function(index){
if(this.cards[index].buy_count>0){
this.cards[index].buy_count--;
this.calc();
}
if (this.cards[index].buy_count == 0 && this.cards[index].checked == true) {
this.cards[index].checked = false
}
},
add:function(index){

var currNums = this.ckeckCurrNums()
if ((currNums + 1) > this.order.count) {
alert("抵扣次数不能大于购买次数！")
return
}
if(this.cards[index].total_time_points==this.cards[index].buy_count){
alert("该卡剩余次数不足！")
return;
}
this.cards[index].buy_count++;
this.cards[index].checked=true;
this.calc();
},

ckeckCurrNums () {

var curritem = this.cards.filter(m => m.checked == true)

var value = 0
curritem.forEach(item => {
value += item.buy_count;
})
return value
},

removeCard: function(id,index) {
this.$http.post("/wemovie/card_order.php?act=delBindCard", {
bind_id: id
}).then((res) => {
console.log(res)
if(res.data.code == 200) {
delete this.cards.splice(index,1);
this.calc();
}else{
alert(res.data.message)
}
})
},

toAddCard: function() {
this.$router.push({
name: "addCard1"
})
},

checkedboxClick (index) {


if (this.cards[index].checked == false) {
this.cards[index].buy_count = 0;
}
this.calc()
},

calc () {
var curritem = this.cards.filter(m => m.checked == true)
var valObj = {}
curritem.forEach(item => {
var key = item.id
valObj[key] = item.buy_count
})
this.valObj=valObj;
console.log(valObj)
this.$http.post("/wemovie/card_order.php?act=index", {
order_id: this.order_id,
params: valObj
}).then((res) => {
console.log(res)
if(res.data.code == 200) {
this.order = res.data.data.order;

} else {
alert(res.data.message);
}
})
},
toPay: function() {
this.$http.post('/wemovie/card_order.php?act=done', {
order_id: this.order_id,
params: this.valObj
}).then((res) => {
if(res.data.code == 200) {
console.log(res)
if(res.data.data.order.is_diff == "false") {

this.$http.post('/wemovie/card_order.php?act=actPay', {
order_id: this.order_id
}).then((res) => {
console.log(res)
if(res.data.code == 200) {
this.$router.replace({
name: "payOk"
})
}else if(res.data.code==458){
alert(res.data.message);
}
})
} else {
this.$router.push({
name: 'mtPayNext',
params: {
order_id: this.order_id
}
})
}
} else {
console.log(res);
alert(res.data.message)
}
})
},
},
destroyed () {
clearInterval(timer)
}
}



