
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

var	timer;//定时器对象
export default{
data(){
return{
order:{},
countDown_minute:"",
countDown_second:"",
radio:["wx"],
wxParams:"",//微信支付需要的参数
order_id:this.$route.params.order_id,
}
},
components:{
Group,
Cell,
Radio,
XButton
},
methods:{
wePay:function(){
console.log("1")
this.callpay();
},
jsApiCall(){//调用微信支付的方法
var _self=this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest',
JSON.parse(this.wxParams),
function(res){
WeixinJSBridge.log(res.err_msg);
if(res.err_msg=='get_brand_wcpay_request:ok'){
_self.$http.post("/wemovie/card_order.php?act=actPay",{order_id:_self.order_id}).then((data)=>{
if(data.data.code == 200){
_self.$router.replace({
name:"orderList"
})

}else{
alert(data.data.message);
}
})
}else{
alert("支付失败！");

}
}
);
},
callpay(){//微信支付浏览器的兼容
if(typeof WeixinJSBridge == "undefined"){
if( document.addEventListener ){
document.addEventListener('WeixinJSBridgeReady', this.jsApiCall, false);
}else if (document.attachEvent){
document.attachEvent('WeixinJSBridgeReady', this.jsApiCall());
document.attachEvent('onWeixinJSBridgeReady', this.jsApiCall());
}
}else{
this.jsApiCall();
}
}
},
created:function(){
this.$http.post("/wemovie/card_order.php?act=payParams",{order_id:this.order_id}).then((res)=>{
if(res.data.code==200){
var _self=this;
console.log(res)
this.order=res.data.data.order;
this.wxParams=res.data.data.params;
var time=res.data.data.order.end_paytime;
timer=setInterval(function(){
if(time>=0){
var minute=Math.floor((time / 60) % 60);
var second=Math.floor(time % 60);
minute  = minute < 10 ? "0" + minute : minute;
second  = second < 10 ? "0" + second : second;
_self.countDown_minute=minute;
_self.countDown_second=second;
time--;
console.log("2")
}else{
clearInterval(timer);
alert("订单已超时！")
};
},1000);
}else{
alert(res.data.message)
}
})
},
destroyed () {
clearInterval(timer);
}
}



