

import Vue from 'vue'
import FastClick from 'fastclick'

import router from './router'
import App from './App'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

import WechatPlugin from 'vux/src/plugins/wechat/index.js'

import stores from './store/store.js'
import qs from 'qs'
Vue.use(WechatPlugin)
Vue.use(AjaxPlugin)
FastClick.attach(document.body)
Vue.config.productionTip = false
router.beforeEach((to, from, next) => {
Vue.http.get("/wemovie/login.php?action=checkLogin").then(res=>{
if(res.data.code==401){
window.location.href="/wemovie/login.php";
}else{
next()
}
})

})





Vue.http.interceptors.request.use(function(config){
stores.dispatch('showloader');
config.data = qs.stringify(config.data);
return config
},function(err){
return Promise.reject(err)
});
Vue.http.interceptors.response.use(function(response){
stores.dispatch('hideloader')
return response
},function(err){
return Promise.reject(err)
});


Vue.http.get("/wemovie/wxscancode.php?act=getScanParams").then((res) => {
if(res.data.code == 200) {
var appId = res.data.data.appId;
var nonceStr = res.data.data.nonceStr;
var signature = res.data.data.signature;
var timestamp = res.data.data.timestamp;
Vue.wechat.config({

appId: appId,
timestamp: timestamp,
nonceStr: nonceStr,
signature: signature,
jsApiList: [

'scanQRCode',
'openLocation',
'getLocation'
]
})
}
});
/* eslint-disable no-new */
new Vue({
router,
store:stores,
render: h => h(App)
}).$mount('#app-box')
var wAlert = window.alert;

window.alert = function (message) {
try {
var iframe = document.createElement("IFRAME");
iframe.style.display = "none";
iframe.setAttribute("src", 'data:text/plain,');
document.documentElement.appendChild(iframe);
var alertFrame = window.frames[0];
var iwindow = alertFrame.window;
if (iwindow == undefined) {
iwindow = alertFrame.contentWindow;
}
iwindow.alert(message);
iframe.parentNode.removeChild(iframe);
}
catch (exc) {
return wAlert(message);
}
}


